import React, { Component } from "react";
import {
  Navbar,
  Container,
  Brand,
  Form,
  FormControl,
  Button,
  Nav,
  Link,
  NavDropdown,
} from "react-bootstrap";

export default class Navigation extends Component {
  render() {
    const Gerb = "https://soliq.uz/assets/images/gerb.png",
      Flag = "https://soliq.uz/assets/images/flag2.png";
    return (
      <>
        <Navbar expand="lg">
          <Container>
            <Navbar.Brand>
              <a href="/" className="brandImg">
                <img src={Flag} alt="" />
                <img src={Gerb} alt="" />
              </a>
              <div className="brandName">
                <span className="name">
                  <h4>Buloqboshi tuman</h4>
                  <h4>Davlat Soliq Inspeksiyasi</h4>
                </span>
                <span className="phone">
                  <i class="fas fa-phone-alt"></i>
                  <h3 className="px-1">+99874 7732569</h3>
                </span>
              </div>
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav
                className="me-auto my-2 my-lg-0"
                style={{ maxHeight: "100px" }}
              >
                <Nav.Link href="#action1">INSPEKSIYA HAQIDA</Nav.Link>
                <Nav.Link href="#action2">FAOLIYAT</Nav.Link>
                <Nav.Link href="#action3">ME'YORIY HUJJATLAR</Nav.Link>
                <Nav.Link href="#action4">XIZMATLAR</Nav.Link>
                <NavDropdown title="TIL" id="navbarScrollingDropdown">
                  <NavDropdown.Item href="#action5">
                    O'zbekcha
                    <img
                      src="https://cdn.countryflags.com/thumbs/uzbekistan/flag-round-250.png"
                      alt=""
                    />
                  </NavDropdown.Item>
                  <NavDropdown.Item href="#action6">
                    Русский
                    <img
                      src="https://cdn.countryflags.com/thumbs/russia/flag-round-250.png"
                      alt=""
                    />
                  </NavDropdown.Item>
                </NavDropdown>
              </Nav>
              <Form className="d-flex">
                <FormControl
                  type="search"
                  placeholder="Search"
                  className="me-2"
                  aria-label="Search"
                />
                <Button variant="outline-primary">Search</Button>
              </Form>
            </Navbar.Collapse>
            <div className="brn">
              <span className="name">
                <h6>Buloqboshi tuman</h6>
                <h6>Davlat Soliq Inspeksiyasi</h6>
              </span>
            </div>
          </Container>
        </Navbar>
      </>
    );
  }
}
