

import React, { Component } from 'react'
import News from './News'
import SlickSlider from './SlickSlider'

export default class Main extends Component {
    render() {
        return (
            <main className=''>
                <div className="row">
                    <div className="col-md-6">
                        <SlickSlider />
                    </div>
                    <div className="col-md-6 pt-5">
                        <News />
                    </div>
                </div>
            </main>
        )
    }
}
